import {Component} from "@angular/core";

import {State} from "clarity-angular";
import {FetchResult, Inventory} from "../inventory/inventory";
import {User} from "../inventory/user";
import {EXAMPLES} from "./examples";

@Component({
    selector: "clr-datagrid-server-driven-demo",
    providers: [Inventory],
    template: require('./server-driven.html'),
    styles: [require('../datagrid.demo.scss')]
})
export class DatagridServerDrivenDemo {
    examples = EXAMPLES;
    users: User[];
    total: number;
    loading: boolean = true;

    constructor(private inventory: Inventory) {
        inventory.size = 103;
        this.inventory.latency = 500;
        inventory.reset();
    }

    refresh(state: State) {
        this.loading = true;
        let filters: {[prop: string]: any[]} = {};
        if (state.filters) {
            for (let filter of state.filters) {
                let {property, value} = <{property: string, value: string}>filter;
                filters[property] = [value];
            }
        }
        this.inventory.filter(filters)
            .sort(<{by: string, reverse: boolean}>state.sort)
            .fetch(state.page.from, state.page.size)
            .then((result: FetchResult) => {
                this.users = result.users;
                this.total = result.length;
                this.loading = false;
            });
    }
}



