module.exports = "<section class=\"dox-content\">\n  <h1>Component Status</h1>\n\n  <p>\n      The Clarity team is building a robust library of components as quickly as possible. Check back often for updates.\n      If a component you need is not listed here, <a href=\"https://github.com/vmware/clarity/issues\">file a GitHub issue</a>.\n  </p>\n\n  <p class=\"component-legend\">\n      <span class=\"component-legend-item\">\n        <span class=\"symbol\"><status-dot [status]=\"20\"></status-dot></span>\n          <span class=\"terminology\">Done</span>\n      </span>\n      <span class=\"component-legend-item\">\n          <span class=\"symbol\"><status-dot [status]=\"10\"></status-dot></span>\n          <span class=\"terminology\">In progress</span>\n      </span>\n      <span class=\"component-legend-item\">\n          <span class=\"symbol\"><status-dot [status]=\"5\"></status-dot></span>\n          <span class=\"terminology\">In queue</span>\n      </span>\n      <span class=\"component-legend-item\">\n          <span class=\"symbol\"><status-dot [status]=\"-1\"></status-dot></span>\n          <span class=\"terminology\">n/a</span>\n      </span>\n  </p>\n\n  <table class=\"table table-condensed table-component-list bump-down\">\n      <thead>\n        <th>Item</th>\n        <th>UX</th>\n        <th>UI</th>\n        <th class=\"hidden-xs-down\">Core</th>\n        <th>NG</th>\n        <th class=\"hidden-xs-down\">Docs</th>\n        <th class=\"hidden-xs-down\">Comments</th>\n      </thead>\n      <tbody>\n        <tr *ngFor=\"let item of components\">\n          <td>\n            <a *ngIf=\"item.url\" [routerLink]=\"getUrl(item)\" [fragment]=\"getFragment(item)\">{{item.text}}</a>\n            <span *ngIf=\"!item.url\">{{item.text}}</span>\n          </td>\n          <td>\n            <status-dot [status]=\"item.ux\"></status-dot>\n          </td>\n          <td>\n            <status-dot [status]=\"item.ui\"></status-dot>\n          </td>\n          <td class=\"hidden-xs-down\">\n            <status-dot [status]=\"item.core\"></status-dot>\n          </td>\n          <td>\n            <status-dot [status]=\"item.ng\"></status-dot>\n          </td>\n          <td class=\"hidden-xs-down\">\n            <status-dot [status]=\"item.dox\"></status-dot>\n          </td>\n          <td class=\"hidden-xs-down\">\n            {{ item.comments }}\n          </td>\n        </tr>\n      </tbody>\n  </table>\n</section>\n"

